import React, { useEffect } from 'react'
import { Card, List, Image, Icon } from "semantic-ui-react";
import { CarouselProvider, Slider, CarouselContext, WithStore, Slide } from "pure-react-carousel";

const FirstSlide = ({ index, setNextLabel, mode, nextStatus, setSkip, skip, setModelHeading }) => {

  useEffect(() => {
    nextStatus(false)
    setSkip(false)
    setModelHeading("Instructions")
    setNextLabel('Let\'s start')
  }, [])
  return (
    // <Slide index={index}>
    <div style={{ margin: '10px' }}>
      <Card>
        <Card.Content>
          <Card.Header>Instructions for the next 10 words</Card.Header>
        </Card.Content>
        <Card.Content>
        {
          mode == 'with_images' && <ol>
          <li>Read each word.</li>
          <li>Play the audio</li>
          <li>Come up with an association that reminds you of how the word in Japanese sounds.</li>
          <li>In English describe the scene linking the association with the word.</li>
          <li>Click on "Generate images".</li>
          <li>Select the image that in your opinion best represents your description.</li>
          <li>Click on "Next"</li>
          <li>Answer a short questionnaire.</li>
        </ol>
        }
        {
          mode != 'with_images' && <ol>
          <li>Read each word.</li>
          <li>Play the audio</li>
          <li>Come up with an association that reminds you of how the word in Japanese sounds.</li>
          <li>In English describe the scene linking the association with the word.</li>
          <li>Click on "Next"</li>
          <li>Answer a short questionnaire.</li>
        </ol>
        }
          {/* <List ordered selection verticalAlign='middle'>
            <List.Item>
              Read each word.
            </List.Item>
            <List.Item>
              Play the audio
            </List.Item>
            <List.Item>
              Generate association in any language.
            </List.Item>
            <List.Item>
              In English describe the scene which reminds you of it.
            </List.Item>
            <List.Item>
              Click generate image.
            </List.Item>
            <List.Item>
              Answer a short questionnaire.
            </List.Item>
          </List> */}
        </Card.Content>
      </Card>
    </div>
    // </Slide>
  )
}

export default FirstSlide


