import React, { useEffect, useState } from 'react'
import { Modal, Button } from 'semantic-ui-react'

import { ClientJS } from 'clientjs';

import CardCarousel from '../CustomCardSlide/CardCarousel'
import './Modal.css'

const ModalComponent = ({ mode, words, toggleMode, currentIteration, setCurrentIteration }) => {
  const [open, setOpen] = useState(false)

  const [size, setSize] = useState('small')
  // const [size, setSize] = useState('tiny')

  const [ip, setIp] = useState('')
  const [fingerprint, setFingerprint] = useState('')

  const [dimmerClose, setDimmerClose] = useState(false)

  const [modelHeading, setModelHeading] = useState('VocabulARy replicated')

  const adjustSize = (s) => {
    setSize(s)
  }

  const show = () => setOpen(true)
  const close = () => setOpen(false)

  // const setSize = (size) => setState({ size })

  // const { open } = 

  useEffect(() => {

    const client = new ClientJS();
    const fingerprint = client.getFingerprint();
    setFingerprint(fingerprint)

    fetch("https://api.ipdata.co?api-key=bdd6192c9ce6c67cc27cab8530b59a2911d4cf735aa7e31763b51506")
      // fetch("https://geolocation-db.com/json/")
      .then(response => {
        return response.json();
      }, "jsonp")
      .then(res => {
        setIp(res.ip)
        console.log('ip', res.ip, 'fingerprint', fingerprint)
      })
      .catch(err => {
        alert(err)
        // console.log(err)
      })

  }, [])
  return (
    <div>
      <Button fluid size='medium' color='teal' className="open-modal" onClick={() => show()}>
        Start Practice session
      </Button>
      <Modal
        // closeIcon
        className="modal-wrapper"
        size={size}
        open={open}
        onClose={() => close()}
        // closeIcon={true}
        closeOnEscape={dimmerClose}
        closeOnDimmerClick={dimmerClose}
      >
        <Modal.Header>
          {modelHeading}
        </Modal.Header>
        <Modal.Content>
          <CardCarousel
            close={close}
            setCurrentIteration={setCurrentIteration}
            currentIteration={currentIteration}
            mode={mode}
            toggleMode={toggleMode}
            words={words}
            ip={ip}
            fingerprint={fingerprint}
            setSize={adjustSize}
            setDimmerClose={setDimmerClose}
            setModelHeading={setModelHeading} />

        </Modal.Content>
        {/* <Modal.Actions>
          <button className="modal-close" onClick={ () => close()}>
            <p>Close</p>
          </button>
        </Modal.Actions> */}
      </Modal>
      <div id="fingerprint" style={{ display: 'none' }}>{fingerprint}</div>
    </div>
  )
}

export default ModalComponent



