import React, { Component, useEffect, useState } from 'react';
import { Segment, Grid, Form, Checkbox, Button, Header, Card, Icon, Image } from 'semantic-ui-react';
import './Slider.css';
import book_honey from '../../images/book_honey.png'
import window_fish from '../../images/window_fish.png'

import imgBook from '../../images/book.jpg'
import imgWindow from '../../images/window.png'

import C from './C'
import Example from './Example'

import Modal from '../Modal/Modal'

import genImg1 from '../../images/stingray_1.png'
import genImg2 from '../../images/stingray_2.png'
import genImg3 from '../../images/stingray_3.png'
import genImg4 from '../../images/stingray_4.png'

import genImg5 from '../../images/book_1.png'
import genImg6 from '../../images/book_2.png'
import genImg7 from '../../images/book_3.png'
import genImg8 from '../../images/book_4.png'

import book from '../Audio/book.mp3'

import lamp from '../Audio/lamp.mp3'
import bed from '../Audio/bed.mp3'
import clock from '../Audio/clock.mp3'
import bookshelf from '../Audio/bookshelf.mp3'
import mirror from '../Audio/mirror.mp3'
import pillow from '../Audio/pillow.mp3'
import telephone from '../Audio/telephone.mp3'
import dressingTable from '../Audio/dressingTable.mp3'
import vase from '../Audio/vase.mp3'
import flower from '../Audio/flower.mp3'
import car from '../Audio/car.mp3'
import tree from '../Audio/tree.mp3'
import radio from '../Audio/radio.mp3'
import shirt from '../Audio/shirt.mp3'
import trouser from '../Audio/trouser.mp3'
import slippers from '../Audio/slippers.mp3'
import rabbit from '../Audio/rabbit.mp3'
import elephant from '../Audio/elephant.mp3'
import zebra from '../Audio/zebra.mp3'
import giraffe from '../Audio/giraffe.mp3'
import bicycle from '../Audio/bicycle.mp3'
import shoes from '../Audio/shoes.mp3'
import ring from '../Audio/ring.mp3'
import cupboard from '../Audio/cupboard.mp3'
import socks from '../Audio/socks.mp3'
import windowAudio from '../Audio/window.mp3'

import imgLamp from '../../images/lamp.png'
import imgBed from '../../images/bed.png'
import imgClock from '../../images/clock.png'
import imgBookshelf from '../../images/bookshelf.png'
import imgMirror from '../../images/mirror.png'
import imgPillow from '../../images/pillow.png'
import imgTelephone from '../../images/telephone.png'
import imgDressingTable from '../../images/dressingTable.png'
import imgVase from '../../images/vase.png'
import imgFlower from '../../images/flower.png'
import imgCar from '../../images/car.png'
import imgTree from '../../images/tree.png'
import imgRadio from '../../images/radio.png'
import imgShirt from '../../images/shirt.png'
import imgTrouser from '../../images/trouser.png'
import imgSlippers from '../../images/slippers.png'
import imgRabbit from '../../images/rabbit.png'
import imgElephant from '../../images/elephant.png'
import imgZebra from '../../images/zebra.png'
import imgGiraffe from '../../images/giraffe.png'
import imgBicycle from '../../images/bicycle.png'
import imgShoes from '../../images/shoes.png'
import imgRing from '../../images/ring.png'
import imgCupboard from '../../images/cupboard.png'
import imgSocks from '../../images/socks.png'

import "pure-react-carousel/dist/react-carousel.es.css";
// import CardCarousel from "./CardCarousel";

const Slider = (params) => {

  const [objArray, setObjArray] = useState([
    {
      example: 1,
      japaneseWord: 'Mado',
      engTranslation: 'Window',
      associationTopic: 'Association in Sinhala language',
      association: 'Madu (stingray)',
      description: 'Window shaped like stingray fish',
      img: imgWindow,
      sound: windowAudio
    },
    {
      example: 2,
      japaneseWord: 'Hon',
      engTranslation: 'Book',
      associationTopic: 'Association in English',
      association: 'Honey',
      description: 'Honey drips on a book',
      img: imgBook,
      sound: book
    },
  ])

  const [mode, setMode] = useState('')
  const [currentIteration, setCurrentIteration] = useState(1)

  const [firstWordSet, setFirstWordSet] = useState([])
  const [secondWordSet, setSecondWordSet] = useState([])

  const [words, setWords] = useState([])

  const toggleMode = () => {
    setMode(mode == 'with_images' ? 'without_images' : 'with_images')
    setWords(secondWordSet)
  }

  useEffect(() => {

    const params = new URLSearchParams(window.location.search) // id=123
    setMode(params.get('mode')) // values: with_images, without_images

    const fw = [
      {
        eng: 'Bookshelf',
        japanese: 'Hondana',
        sound: bookshelf,
        image: imgBookshelf
      },
      {
        eng: 'Lamp',
        japanese: 'Ranpu',
        sound: lamp,
        image: imgLamp
      },
      {
        eng: 'Telephone',
        japanese: 'Denwa',
        sound: telephone,
        image: imgTelephone
      },
      {
        eng: 'Flower',
        japanese: 'Hana',
        sound: flower,
        image: imgFlower
      },
      {
        eng: 'Radio',
        japanese: 'Musen',
        sound: radio,
        image: imgRadio
      },
      {
        eng: 'Bicycle',
        japanese: 'Jitensha',
        sound: bicycle,
        image: imgBicycle
      },
      {
        eng: 'Socks',
        japanese: 'Kutsushita',
        sound: socks,
        image: imgSocks
      },
      {
        eng: 'Shoes',
        japanese: 'Kutsu',
        sound: shoes,
        image: imgShoes
      },
      {
        eng: 'Rabbit',
        japanese: 'Usagi',
        sound: rabbit,
        image: imgRabbit
      },
      {
        eng: 'Elephant',
        japanese: 'Zō',
        sound: elephant,
        image: imgElephant
      },
    ].sort(() => Math.random() - 0.5)
    const sw = [
      {
        eng: 'Cupboard',
        japanese: 'Shokkitana',
        sound: cupboard,
        image: imgCupboard
      },
      {
        eng: 'Clock',
        japanese: 'Tokei',
        sound: clock,
        image: imgClock
      },
      {
        eng: 'Vase',
        japanese: 'Kabin',
        sound: vase,
        image: imgVase
      },
      {
        eng: 'Tree',
        japanese: 'Ki',
        sound: tree,
        image: imgTree
      },
      {
        eng: 'Mirror',
        japanese: 'Kagami',
        sound: mirror,
        image: imgMirror
      },
      {
        eng: 'Car',
        japanese: 'Kuruma',
        sound: car,
        image: imgCar
      },
      {
        eng: 'Trousers',
        japanese: 'Zubon',
        sound: trouser,
        image: imgTrouser
      },
      {
        eng: 'Ring',
        japanese: 'Yubiwa',
        sound: ring,
        image: imgRing
      },
      {
        eng: 'Zebra',
        japanese: 'Shimauma',
        sound: zebra,
        image: imgZebra
      },
      {
        eng: 'Giraffe',
        japanese: 'Kirin',
        sound: giraffe,
        image: imgGiraffe
      },

    ].sort(() => Math.random() - 0.5)

    setFirstWordSet(sw)

    setSecondWordSet(fw)

  }, [])

  useEffect(() => {
    setWords(firstWordSet)
  }, [firstWordSet.length])

  const [index, setIndex] = useState(0)

  const setSkip = (param) => {
    console.log(param)
  }

  const nextStatus = (param) => {
    console.log(param)
  }

  return (
    <Grid centered style={{ backgroundColor: '#fff', paddingTop: '20px' }}>
      <Grid.Row>
        <Header textAlign='center'>
          Think of a word or a phrase in any language you speak that will help you remember a Japanese word.
        </Header>
      </Grid.Row>
      <Grid.Row>
        <Grid.Column textAlign='center'>
          <Header>Examples</Header>
        </Grid.Column>
      </Grid.Row>
      <Grid.Row>
        {
          objArray.length > 0 && <Example
            mode={mode}
            image={objArray[index].img}
            obj={objArray[index]}
            englishWord={objArray[index].engTranslation}
            japaneseWord={objArray[index].japaneseWord}
            sound={objArray[index].sound}
            association={objArray[index].association}
            description={objArray[index].description}
            nextStatus={nextStatus}
            setModelHeading={() => { }}
            setSkip={setSkip}
            generatedImgs={index == 0 ? [genImg1, genImg2, genImg3, window_fish] : [genImg5, genImg6, book_honey, genImg8]}
          />
        }
      </Grid.Row>

      {
        index != 1 && <Grid.Row centered columns={3}>
          <Grid.Column>
            <Button onClick={() => setIndex(index == 0 ? 1 : 0)} fluid color='teal'>{index == 0 ? 'Next Example' : 'Prev Example'}</Button>
            <br />
            {index == 1 && <Modal toggleMode={toggleMode} mode={mode} words={words}
              currentIteration={currentIteration}
              setCurrentIteration={setCurrentIteration}
            />}
          </Grid.Column>
        </Grid.Row>
      }
      {
        index == 1 && <Grid.Row>
          <Grid.Column width={3}>
            <Button size='small' onClick={() => setIndex(index == 0 ? 1 : 0)} fluid color='teal'>{index == 0 ? 'Next Example' : 'Prev Example'}</Button>

          </Grid.Column>
          <Grid.Column width={3}>
            <Modal toggleMode={toggleMode} mode={mode} words={words}
              currentIteration={currentIteration}
              setCurrentIteration={setCurrentIteration}
            />

          </Grid.Column>
        </Grid.Row>
      }
    </Grid>
  )
}
export default Slider



