import React, { useEffect, useState, useRef } from 'react'
import { Card, List, Image, Icon } from "semantic-ui-react";
import { CarouselProvider, Slider, CarouselContext, WithStore, Slide } from "pure-react-carousel";
// import raw from '../NASA/NASA.html';
import ReactHtmlParser from 'react-html-parser';

import ReactDOM from 'react-dom'

var createReactClass = require('create-react-class');
// import __html from '../nasa/NASA.html'
var Iframe = createReactClass({
  render: function () {
    return (
      <div>
        <iframe src={this.props.src} height={this.props.height} width={this.props.width} />
      </div>
    )
  }
});

const ThirdSlide = ({ index, HOST, mode, fingerprint, currentIteration, setNextLabel }) => {

  const [height, setHeight] = useState('0px')
  const iframe = useRef();

  useEffect(() => {
    if(currentIteration == 1){
      setNextLabel('Start the second part')
    }else{
      setNextLabel('Continue for the next questionair')
    }
    
  }, [])

  // useEffect(() => {
  //   console.log('came here')
  //   fetch(raw)
  //     .then(r => r.text())
  //   .then(text => {
  //   console.log('text decoded:', text);
  //   setT(text)
  //   })

  // }, []);
  return (
    // <Slide index={index}>
    <Iframe
      style={{ maxWidth: 640, width: '800px', height: '550px', overflow: 'hidden' }}
      onLoad={() => {
        const obj = ReactDOM.findDOMNode(this);
        // console.log('hello world hello world')
        // console.log(obj)
        setHeight(obj.contentWindow.document.body.scrollHeight + 'px')
        // this.setState({
        //     "iFrameHeight":  obj.contentWindow.document.body.scrollHeight + 'px'
        // });
      }}
      ref={iframe}
      // src="./NASA.html" https://dc85-88-200-63-130.eu.ngrok.io
      
      // src="https://60ab-88-200-63-130.eu.ngrok.io/get_nasa_questionaire/"
      src={HOST + "/get_nasa_questionaire/?fingerprint=" + fingerprint + '&mode=' + mode + '&hasNext=' + ((currentIteration == 1) ? 1: 0)}
      // src='../nasa/NASA.html'
      // src="https://a707-88-200-63-130.eu.ngrok.io/static/public/NASA.html"
      width="100%"
      height="620px"
      scrolling='no'
    />
    // </Slide>
  )
}

export default ThirdSlide


