import React, { useState } from 'react'
import { Card, List, Grid, Icon, Form, Label } from "semantic-ui-react";


const EffortQuestions = ({ index, question, effort: value, setEffort: setValue }) => {


    // const [value, setValue] = useState('none')

    const handleChange = (attr, event) => {
        console.log(event)
        console.log(event.value)
        setValue(index, event.value)
    }

    return (
        <div style={{ margin: '20px 10px 20px 10px' }}>
            <Card>
                <Card.Content header={question} />
                <Card.Content center>
                    {/* <Form.Group> */}
                    <Form.Radio
                        label='1 - No effort'
                        name='effort'
                        value='not_effort'
                        checked={value[index] === 'not_effort'}
                        onChange={(attr, event) => handleChange(attr, event)}
                    />
                    <Form.Radio
                        label='2 - Slight effort'
                        name='effort'
                        value='slightly_effort'
                        checked={value[index] === 'slightly_effort'}
                        onChange={(attr, event) => handleChange(attr, event)}
                    />
                    <Form.Radio
                        label='3 - Some effort'
                        name='effort'
                        value='somewhat_effort'
                        checked={value[index] === 'somewhat_effort'}
                        onChange={(attr, event) => handleChange(attr, event)}
                    />
                    <Form.Radio
                        label='4 - Moderate effort'
                        name='effort'
                        value='modereately_effort'
                        checked={value[index] === 'modereately_effort'}
                        onChange={(attr, event) => handleChange(attr, event)}
                    />
                    <Form.Radio
                        label='5 - Extreme effort'
                        name='effort'
                        value='extreme_effort'
                        checked={value[index] === 'extreme_effort'}
                        onChange={(attr, event) => handleChange(attr, event)}
                    />
                    {/* </Form.Group> */}

                </Card.Content>

            </Card>
        </div>
    )
}

export default EffortQuestions


