import React, { useState, useEffect } from 'react'
import { Card, List, Image, Icon, Form, Label } from "semantic-ui-react";


const Question = ({ index, question, values, setAnswer }) => {

    // const [value, setValue] = useState()

    const handleChange = (attr, event) => {
        console.log(event)
        console.log(event.value)
        setAnswer({
            ...question,
            ['answer']: event.value
        })
    }

    useEffect(() => {
        console.log(question.question, question.answer)
    }, [question.answer])

    return (
        <div style={{ margin: '20px 10px 20px 10px' }}>
            <Card>
                <Card.Content header={question.question} />
                <Card.Content center>
                {
                    values.map(v => 
                        <Form.Radio
                        label={v.label}
                        name={question.question}
                        value={v.value}
                        checked={v.value == question.answer}
                        onChange={(attr, event) => handleChange(attr, event)}
                    />
                    )
                }
                </Card.Content>

            </Card>
        </div>
    )
}

export default Question


