import React, { useState, useEffect } from 'react'
import { Card, Button, Modal, Image, Grid } from 'semantic-ui-react'


const RecallWords = ({
    mode,
    handleRemembered,
    words,
    chosenImgs,
    answers,
    remembered,
    setNextLabel,
    nextStatus
}) => {

    const [leftSeconds, setLeftSeconds] = useState(20)

    const [startTimer, setStartTimer] = useState(false)

    const [intv, setIntv] = useState(null)

    const [isNextDisabled, setNextDisabled] = useState(false)

    const [currentWord, setCurrentWord] = useState()
    const [currentIndex, setCurrentIndex] = useState(-1)

    const [imgVisibility, setImgVisibility] = useState(false)
    const [associationVisibility, setAssociationVisible] = useState(false)
    const [descriptionVisibility, setDescriptionVisible] = useState(false)

    const [tempAssociation, setTempAssociation] = useState('')
    const [tempDescription, setTempDescription] = useState('')

    const timer = () => {
        const timeInterval = setInterval(() => {
            console.log('time interval', leftSeconds)
            setLeftSeconds(leftSeconds - 1)
        }, 1000);
        setIntv(timeInterval)
    }

    useEffect(() => {
        setNextLabel('Next questionnaire')
    }, [])

    useEffect(() => {
        if (leftSeconds == 0){
            if(associationVisibility && leftSeconds == 0){
                setNextDisabled(false)
            }
            clearInterval(intv)
        }
        // if(leftSeconds == 20)
        //     setNextDisabled(false)
        // else   
        //     setNextDisabled(true)
        // if(leftSeconds == 20 && currentIndex > -1)
        //     timer()
    }, [leftSeconds])

    useEffect(() => {
        if (leftSeconds > 0 && startTimer) {
            const timerId = setInterval(() => setLeftSeconds(leftSeconds - 1), 1000);
            return () => clearInterval(timerId);
        }
    }, [leftSeconds, startTimer])


    useEffect(() => {
        console.log('changed next status')
        if(10 - currentIndex - 1 == 0){
            setNextDisabled(true)
        }
        if(currentIndex > 8){
            nextStatus(false)
        }else{
            nextStatus(true)
        }
    },[currentIndex])

    const setNextWord = () => {

        console.log('answers', answers, words[currentIndex + 1])
        let obj = answers.filter(obj => obj.currentWord.eng == words[currentIndex + 1].eng )
        // let obj = answers.filter(obj => obj.currentWord.eng == words[currentIndex + 1].eng && obj.association.length > 0 && obj.description.length > 0)
        console.log('obj', obj)
        if (obj.length > 0) {
            obj = obj[0]
            const { association, description } = obj
            console.log('association', association, 'description', description)

            console.log('current image', chosenImgs[currentIndex + 2])
            // console.log('current image', chosenImgs[(currentIndex + 1) + ''])
            console.log('images', chosenImgs)
            console.log('index', currentIndex + 1)

            setTempAssociation(association)
            setTempDescription(description)

            setCurrentWord(words[currentIndex + 1])
            setCurrentIndex(currentIndex + 1)
            setLeftSeconds(20)
            setStartTimer(true)

        }
        setImgVisibility(false)
        setAssociationVisible(false)
        setDescriptionVisible(false)
        setNextDisabled(true)

        // timer()
    }

    const handleCorrect = () => {
        setStartTimer(false)
        setIntv(null)
        if (associationVisibility) {
            handleRemembered(currentIndex + 1, 20 + (20 - leftSeconds))
        }
        else {
            handleRemembered(currentIndex + 1, 20 - leftSeconds)
        }
        console.log('remembered', answers)
        setNextDisabled(false)
    }

    return (
        <div style={{ margin: '20px 10px 20px 10px' }}>
            <Card>
                <Card.Content header="Please speak out loud the relevant Japanese word" />
                <Card.Content center>
                    <Grid>
                        <Grid.Row>
                            <Grid.Column width={10}>
                                <td>
                                    <tr>
                                        <td>Word: </td>
                                        <td style={{ textAlign: 'right' }}>{(currentWord && currentWord.eng) && <p>{currentWord.eng}</p>}</td>
                                    </tr>
                                    <tr>
                                        <td>Time remaining: </td>
                                        <td style={{ textAlign: 'right' }}>{leftSeconds}</td>
                                    </tr>
                                    <tr>
                                        <td>Words remaining: </td>
                                        <td style={{ textAlign: 'right' }}>{10 - currentIndex - 1}</td>
                                    </tr>
                                    <tr style={{ display: associationVisibility ? '' : 'none' }}>
                                        <td>Association: </td>
                                        <td style={{ textAlign: 'right' }}>{tempAssociation}</td>
                                    </tr>
                                    <tr style={{ display: descriptionVisibility ? '' : 'none' }}>
                                        <td>Description: </td>
                                        <td>{tempDescription}</td>
                                    </tr>
                                </td>
                            </Grid.Column>
                            <Grid.Column width={6}>
                                <td>
                                    {/* <img hidden={!imgVisibility} src={chosenImgs[currentIndex]} /> */}

                                    <Modal size="tiny"
                                        closeIcon
                                        // <Modal visibility={imgVisibility? 'visible': 'none'} size="tiny"
                                        trigger={<Button style={{ display: imgVisibility ? 'block' : 'none' }} color='green' key=""><Image size='tiny' src={chosenImgs[currentIndex + 1]} /> </Button>}
                                    >
                                        <Modal.Content image>
                                            <Image src={chosenImgs[currentIndex + 1]} size="medium" centered />
                                        </Modal.Content>
                                    </Modal>
                                </td>
                            </Grid.Column>
                        </Grid.Row>

                    </Grid>
                    <table style={{ padding: '5px' }}>
                        <tr>


                        </tr>
                    </table>

                </Card.Content>
                <Card.Content extra>
                    <div className='ui three buttons' style={{ padding: '5px' }}>
                        <Button fluid color='green' onClick={() => handleCorrect()}>Correct</Button>
                        <Button disabled={isNextDisabled} fluid color='teal' onClick={() => setNextWord()}>Next word</Button>
                        <Button
                            fluid
                            color='red'
                            disabled={leftSeconds != 0}
                            onClick={() => {
                                console.log('imgVisibility', imgVisibility)
                                setLeftSeconds(20)
                                setStartTimer(true)
                                if (mode == 'with_images') {
                                    setAssociationVisible(true)
                                    setDescriptionVisible(false)
                                    setImgVisibility(true)

                                } else {
                                    setAssociationVisible(true)
                                    setDescriptionVisible(true)
                                    setImgVisibility(false)
                                }
                            }}>Reveal association</Button>
                    </div>
                </Card.Content>
            </Card>
        </div>
    )
}

export default RecallWords


