import React, { useEffect, useState } from 'react'
import { Card, List, Grid, Icon, Form, Label } from "semantic-ui-react";


const DifficultQuestion = ({ index, question, difficulty: value, setDifficulty }) => {


    // const [value, setValue] = useState('none')

    useEffect(() => {
        console.log('came here')
        console.log(index, value)
    }, [])

    const handleChange = (attr, event) => setDifficulty(index, event.value)

    return (
        <div style={{ margin: '20px 10px 20px 10px' }}>
            <Card>
                <Card.Content header={question} />
                <Card.Content center>
                    {/* <Form.Group> */}
                    <Form.Radio
                        label='1 - Very difficult'
                        name='difficulty'
                        value='very_difficulty'
                        checked={value && value[index] === 'very_difficulty'}
                        onChange={(attr, event) => handleChange(attr, event)}
                    />
                    <Form.Radio
                        label='2 - Difficult'
                        name='difficulty'
                        value='difficulty'
                        checked={value && value[index] === 'difficulty'}
                        onChange={(attr, event) => handleChange(attr, event)}
                    />
                    <Form.Radio
                        label='3 - Neutral'
                        name='difficulty'
                        value='neutral'
                        checked={value && value[index] === 'neutral'}
                        onChange={(attr, event) => handleChange(attr, event)}
                    />
                    <Form.Radio
                        label='4 - Easy'
                        name='difficulty'
                        value='easy'
                        checked={value && value[index] === 'easy'}
                        onChange={(attr, event) => handleChange(attr, event)}
                    />
                    <Form.Radio
                        label='5 - Very easy'
                        name='difficulty'
                        value='very_easy'
                        checked={value && value[index] === 'very_easy'}
                        onChange={(attr, event) => handleChange(attr, event)}
                    />
                    {/* </Form.Group> */}

                </Card.Content>

            </Card>
        </div>
    )
}

export default DifficultQuestion


