import React, { useState } from 'react'
import { Card, List, Grid, Icon, Form, Label } from "semantic-ui-react";


const EnglishLvelQuestionnair = ({ question, englishProf: value, setEnglishProf: setValue }) => {


    // const [value, setValue] = useState('none')

    const handleChange = (attr, event) => {
        console.log(event)
        console.log(event.value)
        setValue(event.value)
    }

    return (
        <div style={{ margin: '20px 10px 20px 10px' }}>
            <Card>
                <Card.Content header={question} />
                <Card.Content center>
                    {/* <Form.Group> */}
                    <Form.Radio
                        label='A1: Beginner'
                        name='english'
                        value='beginner'
                        checked={value === 'beginner'}
                        onChange={(attr, event) => handleChange(attr, event)}
                    />
                    <Form.Radio
                        label='A2: Elementary'
                        name='english'
                        value='elementary'
                        checked={value === 'elementary'}
                        onChange={(attr, event) => handleChange(attr, event)}
                    />
                    <Form.Radio
                        label='B1: Intermediate'
                        name='english'
                        value='intermediate'
                        checked={value === 'intermediate'}
                        onChange={(attr, event) => handleChange(attr, event)}
                    />
                    <Form.Radio
                        label='B2: Upper intermediate'
                        name='english'
                        value='upper_intermediate'
                        checked={value === 'upper_intermediate'}
                        onChange={(attr, event) => handleChange(attr, event)}
                    />
                    <Form.Radio
                        label='C1: Advanced'
                        name='english'
                        value='advanced'
                        checked={value === 'advanced'}
                        onChange={(attr, event) => handleChange(attr, event)}
                    />
                    <Form.Radio
                        label='C2: Proficient'
                        name='english'
                        value='proficient'
                        checked={value === 'proficient'}
                        onChange={(attr, event) => handleChange(attr, event)}
                    />
                    {/* </Form.Group> */}

                </Card.Content>

            </Card>
        </div>
    )
}

export default EnglishLvelQuestionnair


