import React, { useState } from 'react'
import { Card, List, Grid, Icon, Form, Label, Input } from "semantic-ui-react";


const GeneralQuestions = ({ question, age, gender, setAge, setGender, setMothertongue, setEmail, email, mothertongue }) => {


    // const [value, setValue] = useState('none')

    // const [gender, setGender] = useState('none')

    const handleGender = (event, attr) => {
        console.log('target', attr.value)
        setGender(attr.value)
    }
    const handleAgeChange = (event, attr) => setAge(attr.value)

    const handleEmailChange = (event, attr) => setEmail(attr.value)

    const handleMotherTongueChange = (event, attr) => setMothertongue(attr.value)
    
    const options = [
        { key: 'm', text: 'Male', value: 'male' },
        { key: 'f', text: 'Female', value: 'female' },
        { key: 'o', text: 'Other', value: 'other' },
    ]

    return (
        <div style={{ margin: '20px 10px 20px 10px' }}>
            <Card>
                <Card.Content header={question} />
                <Card.Content center>
                    <Form>
                        <Grid.Row columns={2}>
                            <Grid.Column width={8}>

                                <Form.Group widths='equal' inline>
                                    <Form.Input
                                        label='How old are you?'
                                        type='number'
                                        value={age}
                                        onChange={(event, attr) => handleAgeChange(event, attr)}
                                    />
                                    <Form.Select

                                        label='Gender'
                                        options={options}
                                        placeholder='Gender'
                                        value={gender}
                                        onChange={(event, attr) => handleGender(event, attr)}
                                        onClick={(e, a) => { console.log(e); console.log(a) }}
                                    />
                                </Form.Group>
                            </Grid.Column>
                            <Grid.Column width={8}>
                                <Form.Input
                                    label="Email"
                                    type="email"
                                    value={email}
                                    onChange={(event, attr) => handleEmailChange(event, attr)}
                                />
                                <Form.Input 
                                    label="Mother tongue"
                                    type="text"
                                    value={mothertongue}
                                    onChange={(event, attr) => handleMotherTongueChange(event, attr)}
                                    />
                            </Grid.Column>
                        </Grid.Row>

                    </Form>
                </Card.Content>

            </Card>
        </div>
    )
}

export default GeneralQuestions


