import React, { useState } from 'react'
import { Card, List, Image, Icon, Form, Label } from "semantic-ui-react";
import { CarouselProvider, Slider, CarouselContext, WithStore, Slide } from "pure-react-carousel";

// import DifficultQuestion from './DifficultQuestion';
// import SatisfiedQuestions from './SatisfiedQuestions';
// import EffortQuestions from './EffortQuestions';

import FirstQuestion from './FirstQuestion';
import Question from './Question';

const FinalQuestionair = ({ index,
    finalFirstQuestion,
    setFinalFirstQuestion,
    whichTechniqueEfficient,
    setWhichTechniqueEfficient,
    whichTechniqueIsEasy,
    setWhichTechniqueIsEasy,
    whichTechniquePreferred,
    setWhichTechniquePreferred,
    rateWithImages,
    setRateWithImages,
    rateWithoutImages,
    setRateWithoutImages,
    wouldUseAgain,
    setWouldUseAgain
}) => {
    
    return (
        // <Slide index={index} style={{ overflowY: 'auto' }}>
        <Form>
            <FirstQuestion
                finalFirstQuestion={finalFirstQuestion}
                setFinalFirstQuestion={setFinalFirstQuestion}
                question="Did you know the techniques used in this study already or have you learned them today?"
            />

            <Question
                question={whichTechniqueEfficient}
                setAnswer={setWhichTechniqueEfficient}
                values={[{
                    value: 'with_images',
                    label: 'With images'
                }, {
                    value: 'without_images',
                    label: 'Without images (imagining them only)'
                }, {
                    value: 'the_same',
                    label: 'The same'
                }, {
                    value: 'dont_know',
                    label: 'I don\'t know'
                }]}
            />
            <Question
                question={whichTechniqueIsEasy}
                setAnswer={setWhichTechniqueIsEasy}
                // name=""
                values={[{
                    value: 'with_images',
                    label: 'With images'
                }, {
                    value: 'without_images',
                    label: 'Without images (imagining them only)'
                }, {
                    value: 'the_same',
                    label: 'The same'
                }, {
                    value: 'dont_know',
                    label: 'I don\'t know'
                }]}
            />
            <Question
                question={whichTechniquePreferred}
                setAnswer={setWhichTechniquePreferred}
                values={[{
                    value: 'with_images',
                    label: 'With images'
                }, {
                    value: 'without_images',
                    label: 'Without images (imagining them only)'
                }, {
                    value: 'the_same',
                    label: 'The same'
                }, {
                    value: 'dont_know',
                    label: 'I don\'t know'
                }]}
            />


            <Question
                question={rateWithImages}
                setAnswer={setRateWithImages}
                values={[{
                    value: 'easy',
                    label: 'Easy'
                }, {
                    value: 'nor_easy_nor_hard',
                    label: 'Nor easy nor hard'
                }, {
                    value: 'hard',
                    label: 'Hard'
                }, {
                    value: 'dont_know',
                    label: 'I don\'t know'
                }]}
            />

            <Question
                question={rateWithoutImages}
                setAnswer={setRateWithoutImages}
                values={[{
                    value: 'easy',
                    label: 'Easy'
                }, {
                    value: 'nor_easy_nor_hard',
                    label: 'Nor easy nor hard'
                }, {
                    value: 'hard',
                    label: 'Hard'
                }, {
                    value: 'dont_know',
                    label: 'I don\'t know'
                }]}
            />

            <Question
                question={wouldUseAgain}
                setAnswer={setWouldUseAgain}
                values={[{
                    value: 'yes',
                    label: 'Yes'
                }, {
                    value: 'no',
                    label: 'No'
                }, {
                    value: 'maybe',
                    label: 'Maybe'
                }, {
                    value: 'dont_know',
                    label: 'I don\'t know'
                }]}
            />

        </Form>
        // </Slide>
    )
}

export default FinalQuestionair


