import React, { useState, useEffect } from 'react'
import { Card, List, Image, Icon, Form, Label } from "semantic-ui-react";
import { CarouselProvider, Slider, CarouselContext, WithStore, Slide } from "pure-react-carousel";

// import DifficultQuestion from './DifficultQuestion';
// import SatisfiedQuestions from './SatisfiedQuestions';
// import EffortQuestions from './EffortQuestions';
import EnglishLvelQuestionnair from './EnglishLevelQuestionnair'
import EducationQuestions from './EducationQuestions';
import GeneralQuestions from './GeneralQuestions';

const DemographyQuestionair = ({ index,
englishProf,
education,
age,
gender,
email,
mothertongue,
setEnglishProf,
setEducation,
setAge,
setGender,
setEmail,
setMothertongue,
setNextLabel
}) => {

    useEffect(() => {
        setNextLabel('End of the study!!!')
    }, [])

    return (
        // <Slide index={index} style={{ overflowY: 'auto' }}>
        <Form>
            <EnglishLvelQuestionnair
                englishProf={englishProf}
                setEnglishProf={setEnglishProf}
                question="Rate level of your english."
            />
            <EducationQuestions
                education={education}
                setEducation={setEducation}
                question="Level of education?"
            />
            <GeneralQuestions
                age={age}
                gender={gender}
                email={email}
                mothertongue={mothertongue}
                setAge={setAge}
                setGender={setGender}
                setEmail={setEmail}
                setMothertongue={setMothertongue}
                question=""
            />
        </Form>
        // </Slide>
    )
}

export default DemographyQuestionair


