import React, { useState, useEffect } from 'react'
import { Card, List, Image, Icon, Form, Label } from "semantic-ui-react";
import { CarouselProvider, Slider, CarouselContext, WithStore, Slide } from "pure-react-carousel";

import DifficultQuestion from './DifficultQuestion';
import SatisfiedQuestions from './SatisfiedQuestions';
import EffortQuestions from './EffortQuestions';
import DifficultOfDescriptionQuestion from './DifficultyOfDescriptionQuestion'

const Questionnair = ({ index, difficulty, satisfied, effort,
    mode,
    difficultyOfDescription,
    setDifficultyOfDescription,
    setDifficulty,
    setSatisfied,
    setEffort,
    nextStatus,
    setSkip,
    setNextLabel
}) => {

    useEffect(() => {
        setSkip(false)
        if(index < 11)
            setNextLabel("Next word")
        else{
            setNextLabel("Recall the words")
        }
    }, [])

    useEffect(() => {

        if(mode == 'with_images'){
            if(difficulty[index] == -1 || satisfied[index] == -1 || effort[index] == -1 || difficultyOfDescription[index] == -1){
                console.log('difficulty')
                console.log(difficulty)
                nextStatus(true)
            }else{
                console.log('hi there')
                console.log(difficulty)
                nextStatus(false)
            }
        }else{
            if(difficulty[index] == -1 || effort[index] == -1 || difficultyOfDescription[index] == -1){
                console.log('difficulty')
                console.log(difficulty)
                nextStatus(true)
            }else{
                console.log('hi there')
                console.log(difficulty)
                nextStatus(false)
            }
        }



    }, [difficulty, satisfied, effort, difficultyOfDescription])

    return (
        <Form>
            <DifficultQuestion
                key={1}
                difficulty={difficulty}
                setDifficulty={setDifficulty}
                index={index}
                question="How difficult was it to come up with an association?"
            />
            <EffortQuestions
                key={2}
                effort={effort}
                setEffort={setEffort}
                index={index}
                question="How much effort did you put into coming up with the association?"
            />
            <DifficultOfDescriptionQuestion
                key={3}
                difficultyOfDescription={difficultyOfDescription}
                setDifficultyOfDescription={setDifficultyOfDescription}
                index={index}
                question="How difficult was it to come up with the description of the association?"
            />
            {
                mode == 'with_images' && <SatisfiedQuestions
                key={4}
                satisfied={satisfied}
                setSatisfied={setSatisfied}
                index={index}
                question="How satisfied are you with the generated image for your association?"
            />
            }
            

        </Form>
    )
}

export default Questionnair


