import React, { useState, useEffect } from 'react'
import { Card, List, Image, Icon, Form, Label } from "semantic-ui-react";


const FirstQuestion = ({ index, question, finalFirstQuestion: value, setFinalFirstQuestion: setValue }) => {

    // const [value, setValue] = useState()

    const handleChange = (attr, event) => {
        console.log(event)
        console.log(event.value)
        setValue(event.value)
    }

    return (
        <div style={{ margin: '20px 10px 20px 10px' }}>
            <Card>
                <Card.Content header={question} />
                <Card.Content center>
                    {/* <Form.Group> */}
                    <Form.Radio
                        label='I knew them already'
                        name='final_first_question_'
                        value='already_knew_no'
                        checked={value == 'already_knew_yes' || value == 'already_knew_no'}
                        onChange={(attr, event) => handleChange(attr, event)}
                    />
                    {
                        (value == 'already_knew_yes' || value == 'already_knew_no') && <ul style={{ listStyle: 'none' }}>
                            <li>Did you also use these techniques to learn the words in a foreign language?</li>
                            <ul style={{ listStyle: 'none' }}>
                                <li>
                                    <Form.Radio
                                        label='Yes'
                                        name='final_first_question'
                                        value='already_knew_yes'
                                        checked={value == 'already_knew_yes'}
                                        onChange={(attr, event) => handleChange(attr, event)}
                                    />
                                </li>
                                <li>
                                    <Form.Radio
                                        label='No'
                                        name='final_first_question'
                                        value='already_knew_no'
                                        checked={value == 'already_knew_no'}
                                        onChange={(attr, event) => handleChange(attr, event)}
                                    />
                                </li>
                            </ul>
                        </ul>
                    }
                    <Form.Radio
                        label='I learnt them today'
                        name='final_first_question'
                        value='i_learnt_them_today'
                        checked={value == 'i_learnt_them_today'}
                        onChange={(attr, event) => handleChange(attr, event)}
                    />
                    <Form.Radio
                        label='I don’t know'
                        name='final_first_question'
                        value='i_dont_know'
                        checked={value === 'i_dont_know'}
                        onChange={(attr, event) => handleChange(attr, event)}
                    />
                    {/* </Form.Group> */}

                </Card.Content>

            </Card>
        </div>
    )
}

export default FirstQuestion


