import React, { useEffect, useState } from 'react'
import { Card, List, Image, Icon, Table, Input, TextArea, Button, Grid, Modal, Dimmer, Loader, Form } from "semantic-ui-react";
import { CarouselProvider, Slider, CarouselContext, WithStore, Slide } from "pure-react-carousel";

import ReactAudioPlayer from 'react-audio-player';

const ExampleSlide = ({
    HOST,
    mode,
    index,
    englishWord,
    japaneseWord,
    sound,
    handleAssociationChanged,
    handleDescriptionChanged,
    association,
    description,
    nextStatus,
    setAssociation,
    setDescription,
    setModelHeading,
    image,
    setNextLabel,
    setSkip }) => {


    const [images, setImages] = useState([])

    const [loading, setLoading] = useState(false)

    const [chosenImg, setChosenImg] = useState('')

    const [modelOpen, setModalOpen] = useState('')

    useEffect(() => {
        setSkip(false)
        nextStatus(true)
        setModelHeading("Practice")
        if(mode != 'with_images'){
            nextStatus(false)
        }
        // setNextLabel('Continue for instructions slide')
        // setAssociation("honey")
        // setDescription("Honeycomb drips on a book")
    }, [])

    // useEffect(() => {
    //     if (association.length > 0 && description.length > 0) {
    //         // if((association && association.length > 0) && (description && description.length > 0)){
    //         nextStatus(false)
    //     } else {
    //         nextStatus(true)

    //     }

    // }, [association, description])
    useEffect(() => {

        if(mode == 'with_images'){
            if (chosenImg.length > 0) {
                nextStatus(false)
            } else {
                nextStatus(true)
            }
        }
        
    }, [chosenImg])

    const generateImages = () => {

        console.log('value', description, description.length == 0)
        console.log(description)
        console.log(description.length)
        setLoading(true)
        // console.log()
        // https://a707-88-200-63-130.eu.ngrok.io
        // http://localhost:5000/generate
        if (description.length == 0)
            return
        fetch(HOST + '/generate', {
            method: 'POST',
            mode: 'cors',
            headers: {
                'Content-Type': 'application/json',
                'Access-Control-Allow-Origin': '*'
            },
            body: JSON.stringify({
                text: description
            })
        })
            .then(res => res.json())
            .then(obj => {
                console.log(obj)
                const imgs = obj.images

                setImages(imgs.filter(img => img.includes('.png')).map(img => HOST + img))
                setLoading(false)
            })
            .catch(e => {
                console.log('error')
                console.log(e)
            })
        setSkip(false)
        // nextStatus(false)
    }

    return (
        // <Slide index={index}>
        <div fluid style={{ margin: '10px' }}>
            <Dimmer active={loading} inverted>
                <Loader content='Generating images!!!' />
            </Dimmer>
            <Table celled>
                <Table.Header>
                    <Table.Row>
                        <Table.HeaderCell></Table.HeaderCell>
                        <Table.HeaderCell>English</Table.HeaderCell>
                        <Table.HeaderCell>Japanese</Table.HeaderCell>
                    </Table.Row>
                </Table.Header>

                <Table.Body>
                    <Table.Row>
                        <Table.Cell>Word</Table.Cell>
                        <Table.Cell>
                            <table style={{ border: 'none' }}>
                                <tr>
                                    <td>{englishWord}</td>
                                    <td>
                                        <Image src={image} size='tiny' />
                                    </td>
                                </tr>
                            </table>
                        </Table.Cell>
                        <Table.Cell verticalAlign='middle' textAlign='center'>
                            <table style={{ border: 'none' }}>
                                <tr>
                                    <td>{japaneseWord}</td>
                                    <td>
                                        <ReactAudioPlayer
                                            style={{ width: '150px' }}
                                            src={sound}
                                            // autoPlay
                                            controls
                                        />
                                    </td>
                                </tr>
                            </table>
                            {/* <Grid.Row columns={2}>
                                    <Grid.Column>Hagaki</Grid.Column>
                                    <Grid.Column>
                                        <ReactAudioPlayer
                                            style={{ width: '100px' }}
                                            src={hagaki}
                                            // autoPlay
                                            controls
                                        />
                                    </Grid.Column>
                                </Grid.Row> */}
                        </Table.Cell>
                    </Table.Row>
                    {/* <Table.Row>
                            <Table.Cell>Audio</Table.Cell>
                            <Table.Cell>
                                <ReactAudioPlayer
                                    style={{ width: '150px' }}
                                    src={postcard}
                                    // autoPlay
                                    controls
                                />
                            </Table.Cell>
                            <Table.Cell>
                                <ReactAudioPlayer
                                    style={{ width: '150px' }}
                                    src={hagaki}
                                    // autoPlay
                                    controls
                                /></Table.Cell>
                        </Table.Row> */}
                    <Table.Row>
                        <Table.Cell>Association</Table.Cell>
                        <Table.Cell colSpan={2}>
                            <Input
                                // disabled
                                onChange={(e) => handleAssociationChanged(e)}
                                // value={"Honey"}
                                style={{ width: '100%' }}
                                placeholder='Word or phrase in any language (eg: Honey)' /></Table.Cell>
                        {/* <Table.Cell positive>
                                <Icon name='close' />
                                Requires call
                            </Table.Cell> */}
                    </Table.Row>
                    <Table.Row>
                        <Table.Cell>Description</Table.Cell>
                        <Table.Cell colSpan={2}>
                            <TextArea
                                // disabled
                                onChange={(e) => handleDescriptionChanged(e)}
                                // value={"Honeycomb drips on a book"}
                                style={{ width: '100%' }} rows={5}
                                placeholder='Description of the scene in English (eg: Honeycomb drips on a book)' />
                        </Table.Cell>
                        {/* <Table.Cell>None</Table.Cell> */}
                    </Table.Row>
                    {
                        mode != 'with_images' && <Table.Row>
                        <Table.Cell colSpan={3}>
                                <Button disabled color="teal" style={{ width: '100%' }} >Done</Button>
                            </Table.Cell>
                        </Table.Row>
                    }

                    {
                        mode == 'with_images' && <Table.Row>

                            <Table.Cell colSpan={3}>
                                <Button disabled={description.length == 0} color="teal" onClick={() => generateImages()} style={{ width: '100%' }} >Generate images</Button>
                                <p>After generating, select best image or try again. You can Imrpove your result by describing the scene in more detail.</p>
                            </Table.Cell>
                            {/* <Table.Cell>None</Table.Cell> */}
                        </Table.Row>
                    }
                    <Table.Row>
                        <Table.Cell colSpan={3}>
                            <Image.Group size='small'>
                                {
                                    images && images.map(img => <Modal open={img == modelOpen} key={img} size="tiny"
                                        trigger={<Button onClick={() => setModalOpen(img)} color={chosenImg == img ? 'green' : 'none'} key={img}><Image style={{ width: '100px' }} src={img} /> </Button>}
                                    >
                                        <Modal.Content image>
                                            <Image src={img} size="medium" centered />
                                        </Modal.Content>
                                        <Modal.Actions>
                                            <Grid>
                                                <Grid.Column floated='left' style={{ width: '200px' }}>
                                                    <Form.Checkbox checked={chosenImg == img} onClick={() => setChosenImg(chosenImg == img ? '' : img)} label="Choose this image" />

                                                </Grid.Column>
                                                <Grid.Column floated='right'>
                                                    <Button style={{ float: 'right' }} color='red' className="modal-close" onClick={() => setModalOpen('')}>
                                                        Close
                                                    </Button>
                                                </Grid.Column>
                                            </Grid>
                                        </Modal.Actions>
                                    </Modal>)

                                    // images && images.map(img => <Modal key={img} size="tiny" closeIcon
                                    //     trigger={<Button color={chosenImg == img ? 'green' : 'none'} key={img}><Image src={img} /> </Button>}
                                    // >
                                    //     <Modal.Content image>
                                    //         <Image src={img} size="medium" centered />
                                    //     </Modal.Content>
                                    //     <Modal.Actions>
                                    //         <tr>
                                    //             <td>
                                    //                 <Form.Checkbox checked={chosenImg == img} onClick={() => setChosenImg(chosenImg == img ? '' : img)} label="Choose this image" />
                                    //             </td>
                                    //             <td></td>
                                    //         </tr>
                                    //     </Modal.Actions>
                                    // </Modal>)
                                }
                            </Image.Group>

                        </Table.Cell>
                        {/* <Table.Cell>None</Table.Cell> */}
                    </Table.Row>
                </Table.Body>
            </Table>
        </div>
        // </Slide>

    )
}

export default ExampleSlide


