import React, { useState } from 'react'
import { Card, List, Grid, Icon, Form, Label } from "semantic-ui-react";


const SatisfiedQuestions = ({ index, question, satisfied: value, setSatisfied: setValue }) => {


    // const [value, setValue] = useState('none')

    const handleChange = (attr, event) => {
        console.log(event)
        console.log(event.value)
        setValue(index, event.value)
    }

    return (
        <div style={{ margin: '20px 10px 20px 10px' }}>
            <Card>
                <Card.Content header={question} />
                <Card.Content center>
                    {/* <Form.Group> */}
                    <Form.Radio
                        label='1 - Very satisfied'
                        name='satisfied'
                        value='very_satisfied'
                        checked={value[index] === 'very_satisfied'}
                        onChange={(attr, event) => handleChange(attr, event)}
                    />
                    <Form.Radio
                        label='2 - Satisfied'
                        name='satisfied'
                        value='satisfied'
                        checked={value[index] === 'satisfied'}
                        onChange={(attr, event) => handleChange(attr, event)}
                    />
                    <Form.Radio
                        label='3 - Neither'
                        name='satisfied'
                        value='neither'
                        checked={value[index] === 'neither'}
                        onChange={(attr, event) => handleChange(attr, event)}
                    />
                    <Form.Radio
                        label='4 - Dissatisfied'
                        name='satisfied'
                        value='dissatisfied'
                        checked={value[index] === 'dissatisfied'}
                        onChange={(attr, event) => handleChange(attr, event)}
                    />
                    <Form.Radio
                        label='5 - Very dissatisfied'
                        name='satisfied'
                        value='very_dissatisfied'
                        checked={value[index] === 'very_dissatisfied'}
                        onChange={(attr, event) => handleChange(attr, event)}
                    />
                    {/* </Form.Group> */}

                </Card.Content>

            </Card>
        </div>
    )
}

export default SatisfiedQuestions


