import React, { useState } from 'react'
import { Card, List, Grid, Icon, Form, Label } from "semantic-ui-react";


const EducationQuestions = ({ question, education: value, setEducation: setValue }) => {


    // const [value, setValue] = useState('none')

    const handleChange = (attr, event) => {
        console.log(event)
        console.log(event.value)
        setValue(event.value)
    }

    return (
        <div style={{ margin: '20px 10px 20px 10px' }}>
            <Card>
                <Card.Content header={question} />
                <Card.Content center>
                    {/* <Form.Group> */}
                    <List>
                        <List.Item>Basic</List.Item>
                        <List.List>
                            <Form.Radio
                                label='1. Primary education'
                                name='education'
                                value='primary_education'
                                checked={value === 'primary_education'}
                                onChange={(attr, event) => handleChange(attr, event)}
                            />
                            <Form.Radio
                                label='2. Lower secondary education'
                                name='education'
                                value='lower_secondary_education'
                                checked={value === 'lower_secondary_education'}
                                onChange={(attr, event) => handleChange(attr, event)}
                            />
                        </List.List>
                        <List.Item>Intermediate</List.Item>
                        <List.List>
                            <Form.Radio
                                label='3. Upper secondary education'
                                name='education'
                                value='upper_secondary_education'
                                checked={value === 'upper_secondary_education'}
                                onChange={(attr, event) => handleChange(attr, event)}
                            />
                            <Form.Radio
                                label='4. Post-secondary non-tertiary education'
                                name='education'
                                value='post_secondary_non_tertiary_education'
                                checked={value === 'post_secondary_non_tertiary_education'}
                                onChange={(attr, event) => handleChange(attr, event)}
                            />
                        </List.List>
                        <List.Item>Advanced</List.Item>
                        <List.List>
                            <Form.Radio
                                label='5. Short-cycle tertiary education'
                                name='education'
                                value='short_cycle_tertiary_education'
                                checked={value === 'short_cycle_tertiary_education'}
                                onChange={(attr, event) => handleChange(attr, event)}
                            />
                            <Form.Radio
                                label='6. Bachelor’s or equivalent level'
                                name='education'
                                value='bachelos_or_equivalent_level'
                                checked={value === 'bachelos_or_equivalent_level'}
                                onChange={(attr, event) => handleChange(attr, event)}
                            />
                            <Form.Radio
                                label='7. Master’s or equivalent level'
                                name='education'
                                value='masters_or_equivalent_level'
                                checked={value === 'masters_or_equivalent_level'}
                                onChange={(attr, event) => handleChange(attr, event)}
                            />
                            <Form.Radio
                                label='8. Doctoral or equivalent level'
                                name='education'
                                value='doctoral_or_equivalent_level'
                                checked={value === 'doctoral_or_equivalent_level'}
                                onChange={(attr, event) => handleChange(attr, event)}
                            />
                            
                        </List.List>
                        <List.Item>Level not stated</List.Item>
                        <List.List>
                            <Form.Radio
                                label='9. Not elsewhere classified'
                                name='education'
                                value='not_elsewhere_classified'
                                checked={value === 'not_elsewhere_classified'}
                                onChange={(attr, event) => handleChange(attr, event)}
                            />
                        </List.List>
                    </List>
                    {/* </Form.Group> */}

                </Card.Content>

            </Card>
        </div>
    )
}

export default EducationQuestions


